<!-- table-->
<template>
  <div class="table">
    <el-table
    id="DataTablePagination"
    v-loading.DataTablePagination="options.loading"
    :data="data"
    :max-height="height"
    :stripe="options.stripe"
    ref="mutipleTable"
    :border="options.border"
    @selection-change="handleSelectionChange">
      <!---->
      <el-table-column v-if="options.mutiSelect" type="selection" style="width: 55px;" >
      </el-table-column>
      <!--end-->
      <!---->
      <template v-for="(column, index) in columns">
        <el-table-column :key="index"  :prop="column.prop" :filters="column.filters" :filter-method="column.handleFilter" :label="column.label" :align="column.align" :width="column.width" :sortable="column.sortable">
          <template slot-scope="scope">
            <template v-if="!column.render">
              <template v-if="column.formatter">
                <span v-html="column.formatter(scope.row, column)"></span>
              </template>
              <template v-else>
                <span>{{scope.row[column.prop]}}</span>
              </template>
            </template>
            <template v-else>
              <expand-dom :column="column" :row="scope.row" :render="column.render" :index="index"></expand-dom>
            </template>
          </template>
        </el-table-column>
      </template>
      <!--end-->

      <!---->
      <el-table-column ref="fixedColumn" :label="i18n.operate" align="center" :width="operates.width" :fixed="operates.fixed"
                       v-if="operates.isShow && operates.data.filter(_x=>_x.show === true).length > 0">
        <template slot-scope="scope">
          <div class="operate-group">
            <template v-for="(btn, key) in operates.data">
              <div class="item" :style="{flex:operates.flex}" :key="key" v-if="btn.show">
                <el-button :class="btn.cls" :type="btn.type" size="mini" :icon="btn.icon" :disabled="btn.disabled"
                           :plain="btn.plain" @click.native.prevent="btn.method(key,scope.row)">{{ btn.label }}
                </el-button>
              </div>
            </template>
          </div>
        </template>
      </el-table-column>
      <!--end-->
    </el-table>
    <!---->
    <el-pagination v-if="pagination" @size-change="handleSizeChange"
                   @current-change="handleIndexChange"
                   :page-size="tableCurrentPagination.limit"
                   :page-sizes="tableCurrentPagination.pageArray" :current-page="tableCurrentPagination.pageIndex"
                   layout="total,sizes, prev, pager, next, jumper"
                   :total="total"
                   :pagination="pagination"></el-pagination>
    <!--end-->
  </div>
</template>
<!-- table end-->
<script>
import '@/utils/i18n.js'
// 
const _pageArray = [15, 25]

export default {
    name: 'DataTablePagination',
    props: {
        data: {
            // 
            // prop:
            // label
            // alignleft, center, right)
            // width:
            type: Array,
            default () {
                return []
            }
        },
        columns: {
            // 
            // prop
            // label
            // align
            // width
            // sortablefalse
            // formatter: (row, column, cellValue) => {return;}
            // render: (h, params) => {return h('el-tag'),{
            //      props: {}
            // }, ''} //el-tagh1,p  
            type: Array,
            default () {
                return []
            }
        },
        operates: {
            // 
            // isShow: 
            // width: 
            // fixedleft,right
            // list
            //      label: 
            //      type :primary / success / warning / danger / info / text
            //      show
            //      icon
            //      plain
            //      disabled
            //      method
            type: Object,
            default () {
                return {}
            }
        },
        total: {
            // 
            type: Number,
            default: 0
        },
        pagination: {
            // 
            // limit:_page_array[0]
            // pageIndex:1
            // pageArray:  _page_array [15, 25]
            type: Object,
            default: null
        },
        otherHeight: {
            // 
            type: Number,
            default: 160
        },
        options: {
            // 
            // stripe  table
            // loading loading
            // highlightCurrentRow 
            // mutiSelect 
            // filter 
            // action  
            // border 
            type: Object,
            default () {
                return {
                    stripe: false,
                    loading: false,
                    mutiSelect: false,
                    highlightCurrentRow: false,
                    filter: false,
                    action: false,
                    border: true
                }
            }
        }
    },
    components: {
        expandDom: {
            functional: true,
            props: {
                row: Object,
                render: Function,
                index: Number,
                column: {
                    type: Object,
                    default: null
                }
            },
            render: (h, ctx) => {
                const params = {
                    row: ctx.props.row,
                    index: ctx.props.index
                }
                if (ctx.props.column) params.column = ctx.props.column
                return ctx.props.render(h, params)
            }
        }
    },
    data () {
        return {
            i18n: {
                operate: gettext("")
            },
            // 
            pageIndex: 1,
            // 
            tableCurrentPagination: {
            },

            // 
            multipleSelection: []
        }
    },
    created () {
    },
    mounted () {
        // pagination   pageArray 
        if (this.pagination && !this.pagination.pageArray) {
            // 
            this.pagination.pageArray = _pageArray
        }
        if (this.pagination && !this.pagination.limit) {
            // 
            this.pagination.limit = this.limit
        }
        if (this.pagination && !this.pagination.pageIndex) {
            // 
            this.pagination.pageIndex = 1
        }
        // 
        this.tableCurrentPagination = this.pagination || {
            limit: this.total,
            pageIndex: 1
        }
    },
    computed: {
        // table
        height () {
            return 1280 - this.otherHeight
            // return this.$utils.Common.getWidthHeight() - this.otherHeight
        }
    },
    methods: {
        //  size
        handleSizeChange (size) {
            if (this.pagination) {
                this.tableCurrentPagination = {
                    // 
                    pageIndex: 1,
                    limit: size,
                    pageArray: _pageArray
                }
                this.$emit('handleSizeChange', this.tableCurrentPagination.limit)
            }
        },
        // 
        handleIndexChange (index) {
            if (this.pagination) {
                // 
                this.tableCurrentPagination.pageIndex = index
                this.$emit('handleIndexChange', this.tableCurrentPagination.pageIndex)
            }
        },
        // 
        handleSelectionChange (val) {
            //  val  multipleSelection 
            this.multipleSelection = val
            this.$emit('handleSelectionChange', val)
        },
        //  
        handleFilter () {
            this.$emit('handleFilter')
        },
        //  
        handleAction () {
            this.$emit('handleAction')
        }
    }
}
</script>

<style lang="scss">
.table {
    .el-pagination {
      float: right;
      margin: 20px;
    }
    .el-table__header-wrapper, .el-table__fixed-header-wrapper {
      thead {
        tr {
          th {
            color: #333333;
          }
        }
      }
    }
    .el-table-column--selection .cell {
      padding: 0;
      text-align: center;
    }
    .el-table__fixed-right {
      bottom: 0 !important;
      right: 6px !important;
      z-index: 1004;
    }
    .operate-group {
      display: flex;
      flex-wrap: wrap;
      .item {
        display: block;
        flex: 0 0 23%;
      }
    }
    .filter-data {
      top: e("calc((100% - 100px) / 3)");
      background-color: rgba(0, 0, 0, 0.7);
    }
    .table-action {
      top: e("calc((100% - 100px) / 2)");
      background-color: rgba(0, 0, 0, 0.7);
    }
    .fix-right {
      position: absolute;
      right: 0;
      height: 100px;
      color: #ffffff;
      width: 30px;
      display: block;
      z-index: 1005;
      writing-mode: vertical-rl;
      text-align: center;
    //   line-height: 28px;
      border-bottom-left-radius: 6px;
      border-top-left-radius: 6px;
      cursor: pointer;
    }
    .btn-size-mini {
        height: 24px;
        line-height: 22px;
        padding: 0 11px;
        font-size: 12px;
    }
  }
</style>
