        async queryPlotlyData (data, id, title) {
            const templateData = await this.queryAtomData(data)
            let total = templateData.data.total
            if (id === 'citeBar' && total === 0) {
                this.citeShow = false
                return
            }
            else if (id === 'citeBar') {
                this.citeShow = true
                this.$nextTick(function () {
                    this.createPolt(id, total, templateData.data.groups, title)
                })
            }
        },
        createPolt (id, total, tableData, title) {
            tableData.sort((val1, val2) => val1.value - val2.value)
            let groupData = []
            let valueData = []
            let propData = []
            let widthList = []
            let size = null
            let length = tableData.length
            if (length === 1) {
                size = 0.1
            }
            else if (length < 3) {
                size = 0.2
            }
            else if (length < 4) {
                size = 0.3
            }
            else if (length < 5) {
                size = 0.5
            }
            else if (length < 10) {
                size = 0.7
            }
            else if (length < 15) {
                size = 0.9
            }
            else {
                size = 0.7
            }
            for (let i = 0; i < tableData.length; i++) {
                groupData.push(tableData[i].name)
                let value = tableData[i].value
                valueData.push(value)
                let fixedVal = (100 * parseFloat(value) / parseFloat(total)).toFixed(2)
                if (isNaN(fixedVal)) {
                    fixedVal = 0
                }
                propData.push(gettext(':') + value + gettext(' :') + fixedVal + '%')
                widthList.push(size)
            }
            const colors = ['#62d1de', '#54d6b6', '#a6db69', '#ffd454', '#ffa361', '#d1d1d1']
            const chartsData = [{
                x: valueData,
                y: groupData,
                type: 'bar',
                orientation: 'h',
                marker: colors,
                hoverinfo: 'text',
                hovertext: propData,
                opacity: 0.6,
                width: widthList
            }]
            const layout = {
                width: 1000,
                height: 600,
                title: title,
                xaxis: {
                    tickvals: valueData,
                    automargin: true
                },
                yaxis: {
                    automargin: true,
                    ticklen: 12
                }
            }
            Plotly.newPlot(id, chartsData, layout, {displayModeBar: false})
        },
        plotlyDataByCite () {
            const data = {
                group_by: 'atom_cite',
                conditions: JSON.stringify({
                    create_time: this.time[0],
                    biz_cc_id: this.biz_cc_id,
                    finish_time: this.time[1]
                })
            }
            this.queryPlotlyData(data, 'citeBar', gettext(''))
        },
        async queryDataTableData (data) {
            const templateData = await this.queryAtomData(data)
            switch (data.group_by) {
                case 'atom_template':
                    this.templateData = templateData.data.groups
                    this.templateTotal = templateData.data.total
                    break
                case 'atom_execute':
