<script>
import '@/utils/i18n.js'
import Plotly from 'plotly.js/dist/plotly-basic'
import Vue from 'vue'
import axios from 'axios'
import {mapActions, mapState} from 'vuex'
import DataTablePagination from '@/components/common/dataTable/DataTablePagination.vue'
import NoData from '@/components/common/base/NoData.vue'

export default {
    name: 'AnalysisAppmaker',
    components: {
        NoData,
        DataTablePagination
    },
    data () {
        return {
            i18n: {
                startAndEndTime: gettext(''),
                startTime: gettext(''),
                endTime: gettext(''),
                choice: gettext(''),
                category: gettext(''),
                businessName: gettext(''),
                appmaker: gettext(''),
                appmakerName: gettext(''),
                prop: gettext(''),
                propName: gettext(''),
                instance: gettext(''),
                instanceName: gettext('')
            },
            appType: 'app',
            category: '',
            biz_cc_id: '',
            instanceData: [],
            time: [0, 0],
            pickerOptions1: {
                disabledDate (time) {
                    return time.getTime() > Date.now()
                },
                shortcuts: [
                    //  
                    {
                        text: gettext(''),
                        onClick (picker) {
                            picker.$emit('pick', [new Date(), new Date()])
                        }
                    },
                    {
                        text: gettext(''),
                        onClick (picker) {
                            const end = new Date()
                            const start = new Date()
                            start.setTime(start.getTime() - 3600 * 1000 * 24)
                            picker.$emit('pick', [start, end])
                        }
                    },
                    {
                        text: gettext(''),
                        onClick (picker) {
                            const end = new Date()
                            const start = new Date()
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7)
                            picker.$emit('pick', [start, end])
                        }
                    },
                    {
                        text: gettext(''),
                        onClick (picker) {
                            const end = new Date()
                            const start = new Date()
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30)
                            picker.$emit('pick', [start, end])
                        }
                    },
                    {
                        text: gettext(''),
                        onClick (picker) {
                            const end = new Date()
                            const start = new Date()
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30 * 3)
                            picker.$emit('pick', [start, end])
                        }
                    }
                ]
            },
            otherHeight: 208, // tabletable
            instanceTotal: 0, // table,
            instancePageIndex: 1, // 
            instanceLimit: 15, // 
            instancePagination: {
                limit: this.instanceLimit,
                pageIndex: this.instancePageIndex,
                pageArray: [15, 25]
            }, // 
            instanceOptions: {
                stripe: true, //  table
                loading: false, // loading
                highlightCurrentRow: true, // 
                mutiSelect: false, // 
                filter: false, // 
                action: true, // 
                border: true // 
            },
            instanceColumns: [
                {
                    prop: 'businessName', // id
                    label: gettext(''), // 
                    align: 'center' // rightleftcenter
                    // width: 600 // 
                },
                {
                    prop: 'templateName', // id
                    label: gettext(''), // 
                    align: 'center', // rightleftcenter
                    // sortable: true // false
                    width: 300 // 
                },
                {
                    prop: 'editTime',
                    label: gettext(''),
                    align: 'center',
                    width: 300
                },
                {
                    prop: 'editor', // id
                    label: gettext(''), // 
                    align: 'center', // rightleftcenter
                    formatter: (row, column, cellValue) => {
                        // 
                        if (row.editor === '' || row.editor === null) {
                            return `<span>--</span>`
                        }
                        return `<span>${row.editor}</span>`
                    }
                    // width: 600 // 
                },
                {
                    prop: 'category', // id
                    label: gettext(''), // 
                    align: 'center' // rightleftcenter
                    // width: 600 // 
                },
                {
                    prop: 'instanceTotal', // id
                    label: gettext(''), // 
                    align: 'center' // rightleftcenter
                    // width: 600 // 
                }
            ],
            instanceOperates: {
                // width: 400,
                // fixed: 'right',
                flex: '0 0 100%',
                isShow: true, // 
                data: [
                    {
                        label: gettext(''), // 
                        show: true, // 
                        cls: 'bk-button bk-primary btn-size-mini',
                        method: (index, row) => {
                            // 
                            this.handleEdit(index, row)
                        }
                    }
                ]
            },
            appmakerShow: true,
            propShow: true,
            noDataHeight: '600px'
        }
    },
    created () {
        // 30
        let date = new Date()
        this.time[1] = Date.parse(date)
        date.setTime(date.getTime() - 3600 * 1000 * 24 * 30)
        this.time[0] = Date.parse(date)
    },
    mounted () {
        this.plotlyDataByBizCCId()
        if (this.categorys.length === 0) {
            this.getCategorys()
        }
        if (this.bizList.length === 0) {
            this.getBizList()
        }
    },
    computed: {
        ...mapState({
            bizList: state => state.bizList,
            categorys: state => state.categorys,
            site_url: state => state.site_url
        })
    },
    methods: {
        ...mapActions('appmaker/', [
            'queryAppmakerData'
        ]),
        ...mapActions([
            'getBizList',
            'getCategorys'
        ]),
        // 
        handleSizeChange (limit) {
            this.limit = limit
            //  pageSize  limit 
        },
        // 
        handleIndexChange (pageIndex) {
            this.pageIndex = pageIndex
            //  pageSize  limit 
        },
        // // 
        handleSelectionChange (val) {
        },
        // 
        instanceHandleSizeChange (limit) {
            this.instancePageIndex = 1
            this.instanceLimit = limit
            //  pageSize  limit 
            this.dataTableDataByinstance()
        },
        // 
        instanceHandleIndexChange (pageIndex) {
            this.instancePageIndex = pageIndex
            //  pageSize  limit 
            this.dataTableDataByinstance()
        },
        // 
        handleEdit (index, row) {
            window.open(this.site_url + 'appmaker/home/' + row.businessId + '/')
        },
        changeTabPanel (name) {
            switch (name) {
                case 'appmaker' :
                    this.plotlyDataByCategory()
                    break
                case 'prop' :
                    this.plotlyDataByBizCCId()
                    break
                case 'instance' :
                    this.dataTableDataByInstance()
                    break
            }
        },
        async queryPlotlyData (data, id, title, seriesTitle) {
            const templateData = await this.queryAppmakerData(data)
            let total = templateData.data.total
            if (id === 'propBar' && total === 0) {
                this.propShow = false
                return
            }
            else if (id === 'propBar') {
                this.propShow = true
                this.$nextTick(function () {
                    this.createPolt(id, total, templateData.data.groups, title)
                })
            }
            else if (id === 'appmakerBar' && total === 0) {
                this.appmakerShow = false
                return
            }
            else if (id === 'appmakerBar') {
                this.appmakerShow = true
                this.$nextTick(function () {
                    this.createPolt(id, total, templateData.data.groups, title)
                })
            }
        },
        createPolt (id, total, tableData, title) {
            tableData.sort((val1, val2) => val1.value - val2.value)
            let groupData = []
            let valueData = []
            let propData = []
            let widthList = []
            let size = null
            let length = tableData.length
            if (length === 1) {
                size = 0.1
            }
            else if (length < 3) {
                size = 0.2
            }
            else if (length < 4) {
                size = 0.3
            }
            else if (length < 5) {
                size = 0.5
            }
            else if (length < 10) {
                size = 0.7
            }
            else if (length < 15) {
                size = 0.9
            }
            else {
                size = 0.7
            }
            for (let i = 0; i < tableData.length; i++) {
                groupData.push(tableData[i].name)
                let value = tableData[i].value
                valueData.push(value)
                let fixedVal = (100 * parseFloat(value) / parseFloat(total)).toFixed(2)
                if (isNaN(fixedVal)) {
                    fixedVal = 0
                }
                propData.push(gettext(':') + value + gettext(' :') + fixedVal + '%')
                widthList.push(size)
            }
            const colors = ['#62d1de', '#54d6b6', '#a6db69', '#ffd454', '#ffa361', '#d1d1d1']
            const chartsData = [{
                x: valueData,
                y: groupData,
                type: 'bar',
                orientation: 'h',
                marker: colors,
                hoverinfo: 'text',
                hovertext: propData,
                opacity: 0.6,
                width: widthList
            }]
            const layout = {
                width: 1000,
                height: 600,
                title: title,
                xaxis: {
                    tickvals: valueData,
                    automargin: true
                },
                yaxis: {
                    automargin: true,
                    ticklen: 12
                }
            }
            Plotly.newPlot(id, chartsData, layout, {displayModeBar: false})
        },
        plotlyDataByCategory () {
            const data = {
                group_by: 'biz_cc_id',
                conditions: JSON.stringify({
                    create_time: this.time[0],
                    biz_cc_id: this.biz_cc_id,
                    finish_time: this.time[1]
                })
            }
            this.queryPlotlyData(data, 'appmakerBar', gettext(''))
        },
        plotlyDataByBizCCId () {
            const data = {
                group_by: 'category',
                conditions: JSON.stringify({
                    create_time: this.time[0],
                    finish_time: this.time[1],
                    category: this.category
                })
            }
            this.queryPlotlyData(data, 'propBar', gettext(''))
        },
        async queryDataTableData (data) {
            const templateData = await this.queryAppmakerData(data)
            switch (data.group_by) {
                case 'appmaker_instance':
                    this.instanceData = templateData.data.groups
                    this.instanceTotal = templateData.data.total
                    break
            }
        },
        dataTableDataByInstance () {
            const data = {
                group_by: 'appmaker_instance',
                conditions: JSON.stringify({
                    create_time: this.time[0],
                    finish_time: this.time[1],
                    biz_cc_id: this.biz_cc_id,
                    category: this.category
                }),
                pageIndex: this.citePageIndex,
                limit: this.citeLimit
            }
            this.queryDataTableData(data)
        }
    }
}
</script>
